import React, {Component} from 'react';
import Table from './Table';
import Form from './Form';
import Header from './Header';

class App extends Component{
    constructor(props){
        super(props);
        //Intial state of the Component
        this.state = {
            users : [
                {
                    name : 'Azman Zaki',
                    city : 'Seri Kembangan',
                },
                {
                    name : 'Zul',
                    city : 'Puchong',
                },
                {
                    name : 'Cikpi',
                    city : 'Meru',
                },
                {
                    name : 'Goh',
                    city : 'Cyberjaya',
                },
            ],
        };
    }
        

        removeUser =(index) => {
            const users = this.state.users;

            //Remove | Change the state
            this.setState({
                users : users.filter( (user, i)=> {
                    return(i !==index);
                })
            });

        }

        addUser =(newUser) =>{
            //Add user | add a user to the array
            this.setState({
                users: [...this.state.users, newUser],
            });
        }
    render(){
        /*const usersArr =[
            {
                name : 'Azman Zaki',
                city : 'Seri Kembangan',
            },
            {
                name : 'Zul',
                city : 'Puchong',
            },
            {
                name : 'Cikpi',
                city : 'Meru',
            },
            {
                name : 'Goh',
                city : 'Cyberjaya',
            },
        ];*/
        return(
            <div>
                <Header siteName="test"/>
                <div className="container mt-4">
                    <h1>Hello World!</h1>
                    <Form handleSubmit={this.addUser} />
                    <Table usersDetails = {this.state.users} delUser={this.removeUser}/>
                </div>
            </div>
        );
    }
}

export default App;