import React, {Component} from 'react';



const TableHeader = () => {
    return(
        <thead>
            <tr>
                <th>No</th>
                <th>Name</th>
                <th>City</th>
            </tr>
        </thead>
    );
}

const TableBody = (props) => {
    //Declarative code
    const rows = props.usersDet.map( (user, index) => {
        return(
            <tr key={index} >
                <td>{index + 1}</td>
                <td>{user.name}</td>
                <td>{user.city}</td>
                <td><button onClick={() => props.remUser(index)}>Delete</button></td>
            </tr>
            
        );
    });
    return(
        <tbody>{rows}
        </tbody>
    );
}


class Table extends Component{
    render(){
        return(
            <table>
                <TableHeader />
                <TableBody usersDet ={this.props.usersDetails} remUser={this.props.delUser} />
                
            </table>
                        
        );
    }
}

export default Table;


