import React, {Component} from 'react';

class Form extends Component {
    constructor(props){
        super(props); // to use 'this' and access parent properties

        this.initialState={
            name:'',
            city:'',
            handle:'',
        };
        //Set the initial state
        this.state = this.initialState;
    }

    handleChange = (event) =>{
        const name = event.target.name;
        const value = event.target.value;
        
        console.log(name + " : " + value);
        this.setState({
            [name]:value,
        });
        console.log(this.state);
    }

    handleSubmit = () =>{
        this.props.handleSubmit(this.state);
        this.setState(this.initialState);
    }


    render(){
        return (
            <div className="card">
                <h4 className = "card-header">Add User </h4>
                <div className = "card-body">
                    <form>
                            <div className = "form-group">
                                <input type = "text" name ="name" placeholder="Enter Name" className="form-control" value={this.state.name} onChange = {this.handleChange} />    
                            </div>
                            <div className = "form-group">
                                <input type = "text" name ="city" placeholder="Enter City" className="form-control" value={this.state.city} onChange = {this.handleChange} />              
                            </div> 
                            <div className = "form-group">
                                <input type = "text" name ="handle" placeholder="Enter Handle" className="form-control" value={this.state.handle} onChange = {this.handleChange} />                     
                            </div>
                            <button type="button" onClick={this.handleSubmit}>Create</button>
                    </form>
                </div>
            </div>
        )
    }
}

export default Form;